.details-section p {
  font-size: 1.6em;
}
.details-section{
  max-width: 65%;
    display: flex;
    flex-direction: column;
    gap: 5em;
}
@media (max-width: 768px) {
  .details-section {
    max-width: 90%; /* Ajusta a largura */
    padding: 1em;
  }

  .details-item {
    font-size: 1em; /* Reduz o tamanho da fonte */
  }
}

@media (max-width: 480px) {
  .details-section {
    padding: 0.5em; /* Padding ainda menor para telas pequenas */
  }

  .details-item {
    font-size: 0.9em; /* Fonte menor */
  }
}