.planet-sidebar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2.2em;
  margin: 5em 4em;
}

button {
  background: none;
  border: none;
  cursor: pointer;
}

button img {
  width: 50px;
  height: 50px;
  opacity: 0.7;
  transition: opacity 0.2s;
}

button img:hover {
  opacity: 1;
}
/* Add this to your Navbar.css */
.add-planet-button {
  background-color: #09b4f7; /* Green */
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 26px;
  cursor: pointer;
  border-radius: 50%;
  width: 54px;
  height: 54px;
  margin-top: 20px;
}

.add-planet-button:hover {
  background-color: #5ab8dd;
  transition: 0.2s;
}
@media (max-width: 768px) {
  .planet-sidebar {
    flex-direction: column; /* Coloca os ícones em uma coluna */
    align-items: flex-start; /* Alinha à esquerda */
    gap: 2em;
  }

  .planet-item img {
    width: 30px; /* Reduz os ícones em telas menores */
    height: 30px;
  }
}
.delete-button {
  font-size: 16px;
  background: none;
  border: none;
  color: red;
  cursor: pointer;
  transition: color 0.2s;
}

.delete-button:hover {
  color: darkred;
}
.planet-item img {
  width: 50px;
  height: 50px;
  opacity: 0.65;
  transition: opacity 0.2s, transform 0.2s;
}

.planet-item.active-planet img {
  opacity: 1; /* Totalmente visível */
  transform: scale(1.15);
}

.planet-item img:hover {
  opacity: 1;
  transform: scale(1.15);
}