.planet-form-container {
    position: fixed;
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
    padding: 20px;
    border-radius: 10px;
    z-index: 1000;
  }
  
  .planet-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .planet-form input {
    padding: 10px;
    border: none;
    border-radius: 5px;
  }
  
  .planet-form button {
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .planet-form button:hover {
    background: #444;
    color: white;
  }
  #submit{
    background-color: #09b4f7;
    color: white;
  }
  #submit:hover{
    background-color: #5ab8dd;
  }
  #cancel{
    background-color: #b42e2e;
    color: white;
  }
  #cancel:hover{
    background-color: #da3232;
  }