/*Fonts*/
@font-face {
  font-family: BykerTrial-Medium;
  src: url(./assets/fonts/BykerTrial-Medium.otf);
}
@font-face {
  font-family: BykerTrial-Regular;
  src: url(./assets/fonts/BykerTrial-Regular.otf);
}
@font-face {
  font-family: BykerTrial-Light;
  src: url(./assets/fonts/BykerTrial-Light.otf);
}
@font-face {
  font-family: BykerTrial-UltraLight;
  src: url(./assets/fonts/BykerTrial-UltraLight.otf);
}

/*General*/
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
}
a{
  text-decoration: none;
  color: white;
}
h1{
  font-family: BykerTrial-Regular;
  font-size: 32px;
}

body {
  margin: 0;
  font-family: Arial, sans-serif;
  color: white;
  background-color: #000;
}

/*Index (Home) Page*/
#home{
  background: url(./assets/images/index/index.jpg) no-repeat center center;
  background-size: cover;
}
.home-content{
  width: 100%;
  display: flex;
  align-items: center;
}
.sidebar{
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4.2em;
  margin: 0 0 0 3em;
}
#main-logo{
  width: 100px;
  position: relative;
  margin-top: 475px;
}
#line-one{
  height: 17em;
  border: 1px solid white;
  opacity: 0.7;
}
#line-two{
  height: 17em;
  border: 1px solid white;
  opacity: 0.7;
}
.arrow{
  position: absolute;
  top: 49%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0.7;
}
.arrow span{
  display: block;
  width: 17px;
  height: 17px;
  border-bottom: 2px solid #ffffff;
  border-right: 2px solid #ffffff;
  transform: rotate(45deg);
  margin: 0 0 7px 0;
  animation: scroll 2s infinite;
}
.arrow span:nth-child(2){
  animation-delay: -0.2s;
}
.arrow span:nth-child(3){
  animation-delay: -0.4s;
}
@keyframes scroll {
  0%{
      opacity: 0;
      transform: rotate(45deg) translate(-20px, -20px);
  }
  50%{
      opacity: 1;
  }
  100%{
      opacity: 0;
      transform: rotate(45deg) translate(20px, 20px);
  }      
  }

/*Intro Page*/
#intro{
  background: url(./assets/images/intro/intro.jpg) no-repeat center center;
  background-size: cover;
}
.intro-content{
  max-width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 12.5em;
  padding: 0 10%;
  color: white;
}
#intro-button{
  background-color: transparent;
  padding: 1em 2em;
  border: 0.1em solid white;
  color: white;
  font-family: BykerTrial-Regular;
  cursor: pointer;
  font-size: 20px;
}
#intro-button:hover{
  background-color: white;
  padding: 1em 2em;
  border: 0.1em solid white;
  color: black;
  font-family: BykerTrial-Regular;
  transition: 0.2s;
}
.author{
  position: absolute;
  bottom: 1%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: 300;
  font-size: 1em;
  font-family: BykerTrial-UltraLight;
}
.author a:hover{
  color: #09b4f7;
  transition: 0.2s;
}

/*Multi Page*/
#image-logo{
  height: auto;
  max-width: 100%;
  width: 100px;
}
#logo-container{
  /*background-image: url("image.jpg");*/
  background-size: 100% 100%;
}
.page{
  background-size: cover;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); 
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; 
}


.planet-form-container {
  background-color: #041c33;
  padding: 20px;
  border-radius: 10px;
  width: 400px; 
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  position: relative;
}


.close-modal-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 30px;
  color: #18aac4;
  cursor: pointer;
}

.close-modal-button:hover {
  color: #1992e4;
}
.main-content{
  max-width: 65%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10em;
  padding: 0 0 10% 10%;
  color: white;
  max-height: 100vh;
}
#title-padding{
  display: flex;
  flex-direction: column;
  gap: 1.5em;
}
#title{
  font-family: BykerTrial-Medium;
  font-size: 76px;
}
p{
  font-family: BykerTrial-UltraLight;
  font-size: 26px;  
}
#planet-buttons{
  display: flex;
  gap: 2.5em;
}
.planet-sidebar{
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2.5em;
  margin: 0 0 0 3.5em;
}
.nav-link {
  opacity: 0.7;
}
.nav-link.active {
  opacity: 1;
}
.fa-house{
  color: #09b4f7;
  font-size: 2.9em;
}
.fa-house:hover{
  color: #7acada;
  transition: 0.2s;
}
#side-planet {
  opacity: 0.8;
  width: 45px;
}
#side-planet:hover{
  opacity: 1;
  transition: 0.2s;
}
#text-description{
  display: flex;
  flex-direction: column;
  gap: 1em;
}
.toggle-edit-button {
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 0.5em 1em;
  background-color: #09b4f7;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.toggle-edit-button:hover {
  background-color: #0078a0;
}
/*Media Queries*/
@media(max-width:1450px){
  .main-content{
    padding: 0 12%;
  }
}

@media (max-width:550px){
  .author{
display: none;
}
}
@media(max-width:760px){
#line-one, #line-two{
      display: none;
}
.sidebar{
  display: none;
}
}
@media (max-width: 1068px) {
  .main-content {
    max-width: 90%;
    padding: 0 16%;
  }

  #title {
    font-size: 28px; 
  }
}
@media (max-width: 768px) {
  .main-content {
    padding: 0 20%; 
  }
}
@media (max-width: 480px) {
  .main-content {
    padding: 0 26%; 
  }

  #title {
    font-size: 24px;
  }
}